import { TOGGLE_SIDE_MENU } from '../constants/actionTypes';

const defaultState = {
  isSideMenuOpen: true
};

export default function(state = defaultState, action) {
  switch (action.type) {
    case TOGGLE_SIDE_MENU:
      return {
        ...state,
        isSideMenuOpen: !state.isSideMenuOpen
      }
    default:
      return state;
  }
}

